import { Injectable } from "@angular/core";
import { CommonService } from "./common/common.service";
import { Observable } from "rxjs";
import { UrlService } from './url/url.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/internal/operators';
import { ApiService } from './api/api.service';

@Injectable({
  providedIn: "root"
})
export class AnalystsService {
  BASE_URL: any = this.url.SERVER_URL;
  apiEndPoints:any;
  constructor(private comm: CommonService,
    private url:UrlService,
    private http: HttpClient,
    private api:ApiService) {
    {
      this.apiEndPoints = {
        //Analysts
        getAllUserProfile: 'admin/getAllUserProfile',
        addAnalyst:'admin/addUser',
        deleteBlockUnBlockDeactivateUserProfile:'admin/deleteBlockUnBlockDeactivateUserProfile'
      }
      for (let key in this.apiEndPoints) {
        this.apiEndPoints[key] = this.BASE_URL + this.apiEndPoints[key];
      }
  }
}
protected getHeaders(): { headers: HttpHeaders } {
   return {
     headers: new HttpHeaders({
    
      'Content-Type': 'application/json',
      'Authorization': this.api.getToken()
     })
   };
 }
 getAllAnalysts(){
    const data={
        "pageNo":1,
        "userType":"ANALYST"
    }
    //usertype--->'ANALYST','ENTHUSIAST'"
  return this.http
  .post<any>(this.apiEndPoints.getAllUserProfile,data,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('All ANALYSTS'))
  );
}

addAnalyst(analyst:any){
    // const data:FormData=new FormData();
    // data.append('email',analyst.email);
    // data.append('userName',analyst.userName);
    // data.append('password',"12345678");
    // data.append('userType',"ANALYST");
   const data={
    'email':analyst.email,
    'userName':analyst.userName,
    'password':"12345678",
    'userType':"ANALYST"
   }
    return this.http
    .post<any>(this.apiEndPoints.addAnalyst,data,this.getHeaders())
    .pipe(
      catchError(this.handleError<any>('ADD ANALYST'))
    );
}

 deleteAnalyst(id:any){
 const data={
    "userId":id,
    "isDeleted":true
 }
  return this.http
  .post<any>(this.apiEndPoints.deleteBlockUnBlockDeactivateUserProfile,data,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('Delete Analyst'))
  );
 }
 blockAnalyst(id:any){
    const data={
       "userId":id,
       "isBlocked":true,
    }
     return this.http
     .post<any>(this.apiEndPoints.deleteBlockUnBlockDeactivateUserProfile,data,this.getHeaders())
     .pipe(
       catchError(this.handleError<any>('Block Analyst'))
     );
    }
 unBlockAnalyst(id:any){
    const data={
        "userId":id,
        "isBlocked":false,
     }
      return this.http
      .post<any>(this.apiEndPoints.deleteBlockUnBlockDeactivateUserProfile,data,this.getHeaders())
      .pipe(
        catchError(this.handleError<any>('UnBlock Analyst'))
      );
    }
 private handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {
    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead
    return;
  };
}
}
