import { Injectable } from "@angular/core";
import { CommonService } from "./common/common.service";
import { Observable } from "rxjs";
import { UrlService } from './url/url.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/internal/operators';
import { ApiService } from './api/api.service';

@Injectable({
  providedIn: "root"
})
export class AlgorithmService {
  BASE_URL: any = this.url.SERVER_URL;
  apiEndPoints:any;
  constructor(private comm: CommonService,
    private url:UrlService,
    private http: HttpClient,
    private api:ApiService) {
    {
      this.apiEndPoints = {
        //Algorithems
        getAllAlgorithem: 'admin/getAllAlgorithem',
        deleteAlgorithem: 'admin/deleteAlgorithem'
      }
      for (let key in this.apiEndPoints) {
        this.apiEndPoints[key] = this.BASE_URL + this.apiEndPoints[key];
      }
  }
}
protected getHeaders(): { headers: HttpHeaders } {
   return {
     headers: new HttpHeaders({
    
      'Content-Type': 'application/json',
      'Authorization': this.api.getToken()
     })
   };
 }
 getAllAlgorithm(){
    const data={
        "pageNo":1,
    }
  return this.http
  .post<any>(this.apiEndPoints.getAllAlgorithem,data,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('All Algorithm'))
  );
}

 deleteAlgorithem(id:any){
 const data={
    "algorithemId":id
 }
  return this.http
  .post<any>(this.apiEndPoints.deleteAlgorithem,data,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('Delete Algorithm'))
  );
 }
 private handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {
    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead
    return;
  };
}
}
